<template>
  <b-card-code title="" no-body>
    <div class="pl-3 pr-3 pb-2">
      <!-- User Info: Input Fields -->
      <b-form class="">
        <!-- Header: Personal Info -->
        <div class="d-flex">
          <feather-icon icon="UserIcon" size="19" />
          <h4 class="mb-2 ml-50">Personal Information</h4>
        </div>

        <b-row>
            <!-- Field: Country -->
          <b-col cols="12" md="6" lg="4">
            <b-form-group label="วันที่รับบริการ">
              <flat-pickr
                class="form-control"
                :config="{ dateFormat: 'Y-m-d' }"
                placeholder="YYYY-MM-DD"
                v-model="form.vstdate"
              />
            </b-form-group>
          </b-col>
          <!-- Field: Username ค้นหาผู้ป่วยด้วย cid หรือชื่อ-->
          <b-col cols="12" md="4">
            <b-form-group label="ผู้ป่วย" label-for="role">
              <v-select
                v-model="form.cid"
                :dir="$store.state.appConfig.isRTL ? 'rtl' : 'ltr'"
                label="title"
                :options="ob_person"
              />
            </b-form-group>
          </b-col>

          <!-- Field: Username -->
          <b-col cols="12" md="4">
            <b-form-group label="อุณหภูมิร่างกาย" label-for="bt">
              <b-form-select v-model="form.bt" :options="ob_bt" />
            </b-form-group>
          </b-col>

          <!-- Field: Full Name -->
          <b-col cols="12" md="4">
            <b-form-group label="ระดับ Oxigen ในเลือด" label-for="O2sat">
              <b-form-input v-model="form.O2sat" />
            </b-form-group>
          </b-col>

          <b-col cols="12" md="4">
            <b-form-group label="อาการ" label-for="medication">
              <b-form-input v-model="form.medication"/>
            </b-form-group>
          </b-col>

          <b-col cols="12" md="4">
             <b-form-group label="บันทึกการรักษา" label-for="note">
              <b-form-input v-model="form.note" />
            </b-form-group>
          </b-col>

       <b-col cols="12" md="4">
            <b-form-group label="ผู้ประเมิน" label-for="role">
              <v-select
                v-model="form.provid"
                :dir="$store.state.appConfig.isRTL ? 'rtl' : 'ltr'"
                label="title"
                :options="ob_provid"
              />
            </b-form-group>
          </b-col>
        </b-row>

        <b-row class="mt-2">
          <b-col>
            <b-button
              variant="primary"
              class="mb-1 mb-sm-0 mr-0 mr-sm-1"
              :block="$store.getters['app/currentBreakPoint'] === 'xs'"
              @click="handleSubmit"
            >
              บันทึก
            </b-button>
            <b-button
              variant="outline-secondary"
              :block="$store.getters['app/currentBreakPoint'] === 'xs'"
            >
              ล้าง
            </b-button>
          </b-col>
        </b-row>
      </b-form>
    </div>
  </b-card-code>
</template>

<script>
import BCardCode from "@core/components/b-card-code/BCardCode.vue";
import {
  BRow,
  BCol,
  BForm,
  BFormGroup,
  BFormInput,
  BFormRadioGroup,
  BFormCheckboxGroup,
  BButton,
  BFormFile,
  BAvatar,
  BFormSelect,
} from "bootstrap-vue";
import flatPickr from "vue-flatpickr-component";
import { ref } from "@vue/composition-api";
import vSelect from "vue-select";
import VueSweetalert2 from 'vue-sweetalert2';
import 'sweetalert2/dist/sweetalert2.min.css';

export default {
  components: {
    BRow,
    BCol,
    BForm,
    BFormFile,
    BAvatar,
    BFormGroup,
    BCardCode,
    flatPickr,
    BFormInput,
    vSelect,
    BFormRadioGroup,
    BFormCheckboxGroup,
    BButton,
    BFormSelect,
  },
  data() {
    return {
      file: "",
      url: null,
      form: {
        vstdate: "",
        cid: "",
        bt:"",
        O2sat: "",
        medication: "",
        note: "",
        provid: "",
      },
      ob_bt:["35","35.5","36","36.5","37","37.5","38","38.5","39","38.5","40","40.5","41","41.5","42","42.5","43","43.5"],
      ob_person: [],
      ob_provid: [],
    };
  },
  mounted() {
    this.$http.get("api/v1/forms/person").then((res) => {
      res.data.result.forEach((element) => {
        this.ob_person.push({
          title: element.fullname,
          value: element.id_card,
        });
      });
    });
    this.$http.get("api/v1/forms/provid").then((res) => {
      res.data.result.forEach((element) => {
        this.ob_provid.push({
          title: element.firstname,
          value: element.id,
        });
      });
    });
  },
  methods: {
    onFileChange(e) {
      const file = e.target.files[0];
      this.file = file;
      this.url = URL.createObjectURL(file);
    },
    handleSubmit() { 
      console.log(this.form)
      this.$http
        .post(`api/v1/covid/hi-add-visit`,  this.form)
        .then((res) => {
          console.log(res.data);

        })
        .catch((err) => console.log(err));
    },
//เดี๋ยวกลับมาไล่
    async fetchAMP() {      
      await this.$http
        .get(`api/v1/forms/district-by-province/${this.form.chwpart.value}`)
        .then((res) => {
          this.ob_amppart = [];
          res.data.result.forEach((element) => {
            this.ob_amppart.push({
              title: element.ampurname,
              value: element.ampurcodefull,
            });
          });
        });
      await this.$http.get(`api/v1/forms/c_hospital/${this.form.chwpart.value}`).then((res) => {
        res.data.result.forEach((element) => {
          this.ob_hospital.push({
            title: element.hosname,
            value: element.hoscode,
          });
        });
      });
    },
    fetchTMB() {
      this.$http
        .get(`api/v1/forms/subdistrict-by-district/${this.form.amppart.value}`)
        .then((res) => {
          this.ob_tmbpart = [];
          res.data.result.forEach((element) => {
            this.ob_tmbpart.push({
              title: element.tambonname,
              value: element.tamboncodefull,
            });
          });
        });
    },
  },
};
</script>

<style lang="scss">
@import "@core/scss/vue/libs/vue-flatpicker.scss";
@import "@core/scss/vue/libs/vue-select.scss";
